<template>
	<view>
	<view class="myhead">
		<view class="userifom" v-if="user.name != ''">
			<view class="headimg">
				<image  :src='user.headimg'></image>
			</view>
			<view class="username">
				<view class="name">{{user.name}}</view>
				<view class="record">
					<view v-if="user.layer_status == 0">立即认证享更多权益</view>
					<view v-if="user.layer_status == 1">认证中</view>
					<view v-if="user.layer_status == 2">已认证</view>
				</view>
			</view>
		</view>
		<view class="userifom" v-else>
			<view class="headimg">
				<image  src='../../static/icon/loginicon.png'></image>
			</view>
			<view class="username">
				<view class="name" style="font-size: 40rpx;font-weight: 700;">请授权</view>
			</view>
		</view>
		<view class="withdraw">
			<view class="balance">
				<view>{{user.balance}}</view>
				<view>余额</view>
			</view>
			<view class="darwbtn" @click="takeOut(user.balance)">立即提现</view>
			<view class="with" @tap="bill()">
				<view>余额明细</view>
				<image src="../../static/images/back.png"></image>
			</view>
		</view>
		<view class="vip" v-if="user.lawyer == ''" @tap="open()">
			<view class="vipdet" v-if="user.package == ''" >
				<view>123法律VIP</view>
				<view>开通即尊享20+项专属特权</view>
			</view>
			<view class="vipname" v-else @tap="open()" >{{user.package.name}}</view>
			<view class="kaitongbtn" @tap="open()" v-if="user.package == ''">立即开通</view>
			<view class="kaitongbtn" v-else>已开通</view>
		</view>
	</view>
	<view class="iconlist">
		<view class="zixun" v-if="user.lawyer == ''">
			<view v-for="(item,index) in iconlist.zixun" class="zixunitem" @click="toPage(item.tourl)" :key="index">
				<image :src='item.url' class="zixunitemimage"></image>
				<view class="zixunitemview">{{item.name}}</view>
			</view>
		</view>
		<view class="zixun" v-if="user.lawyer != ''">
			<view v-for="(item,index) in iconlist1.zixun" class="zixunitem" @click="toPage(item.tourl)" :key="index">
				<image :src='item.url' class="zixunitemimage"></image>
				<view class="zixunitemview">{{item.name}}</view>
			</view>
		</view>
	</view>
	<view class="tuigaung" v-if="user.lawyer == ''">
		<!-- <view class="tuigaung"> -->
		<view class="tgtext">我的推广</view>
		<view class="zixun">
			<button class="yqbtn" open-type="share">
				<image src="../../static/icon/fenxiangicon.png" class="zixunitemimage"></image>
				<view class="zixunitemview" style="margin-top: 22rpx;">邀请好友</view>
			</button>
			<button class="yqbtn" @tap="erm()">
				<image src="../../static/icon/ewm.png" class="zixunitemimage"></image>
				<view class="zixunitemview" style="margin-top: 22rpx;">分享二维码</view>
			</button>
			<view v-for="(item,index) in iconlist2.zixun" class="zixunitem" @click="toPage(item.tourl)" :key="index">
				<image :src='item.url' class="zixunitemimage"></image>
				<view class="zixunitemview">{{item.name}}</view>
			</view>
		</view>
	</view>
	<view class="functionlist">
		<view class="functionitem"@tap="toUrl1()">
			<view class="itemleft">
				<image src="../../static/icon/myicon1.png"></image>
				<view>我的优惠券</view>
			</view>
			<view class="itemright">
				<image src="../../static/icon/myrighticon.png" mode=""></image>
			</view>
		</view>
		<view class="functionitem"@tap="toUrl2()" :class="[user.layer_status == 0?'':'buke']">
			<view class="itemleft">
				<image src="../../static/icon/myicon2.png"></image>
				<view>律师认证</view>
			</view>
			<view class="itemright">
				<view class="renzheng" v-if="user.layer_status == 0">未认证</view>
				<view class="renzheng" v-if="user.layer_status == 2">已认证</view>
				<view class="renzheng" v-if="user.layer_status == 1">认证中</view>
				<image src="../../static/icon/myrighticon.png" mode=""></image>
			</view>
		</view>
		<view class="functionitem"@tap="toUrl3()">
			<view class="itemleft">
				<image src="../../static/icon/myicon3.png"></image>
				<view>常见问题</view>
			</view>
			<view class="itemright">
				<image src="../../static/icon/myrighticon.png" mode=""></image>
			</view>
		</view>
		<view class="functionitem"@tap="toUrl4()">
			<view class="itemleft">
				<image src="../../static/icon/myicon4.png"></image>
				<view>客服</view>
			</view>
			<view class="itemright">
				<view class="kefu" v-if="user.kefu">{{user.kefu}}</view>
				<image src="../../static/icon/myrighticon.png" mode=""></image>
			</view>
		</view>
		<view class="functionitem"@tap="toUrl5()">
			<view class="itemleft">
				<image src="../../static/icon/myicon5.png"></image>
				<view>关于我们</view>
			</view>
			<view class="itemright">
				<view class="kefu" v-if="item.kefu">{{user.kefu}}</view>
				<image src="../../static/icon/myrighticon.png" mode=""></image>
			</view>
		</view>
		<view class="functionitem"@tap="toUrl6()">
			<view class="itemleft">
				<image src="../../static/icon/myicon6.png"></image>
				<view>服务协议</view>
			</view>
			<view class="itemright">
				<view class="kefu" v-if="item.kefu">{{user.kefu}}</view>
				<image src="../../static/icon/myrighticon.png" mode=""></image>
			</view>
		</view>
	</view>
	<view class="huoqu" v-if="xian">
		<image src="../../static/icon/wxgficon.png" mode=""></image>
		<view class="btnbor"></view>
		<view class="shao">需要获取本机手机号</view>
		<button open-type="getPhoneNumber" @getphonenumber="getPhoneNumber">授权登录</button>
	</view>
	<view class="jds" @tap="ancl()" v-if="ewm">
		<view class="baidi">
		<image :src="ewmimage"></image>
		<button class="bendi" @tap="saveImgToLocal()">保存到相册</button>
		</view>
	</view>
	</view>
</template>

<script>
	import iconlist from '../components/iconlist/iconlist.vue'
	export default {
		components:{
			iconlist
		},
		data() {
			return {
				user:{
					uid:1,
					lawyer:'',
					name:'',
					headimg:'',
					balance:0,//余额
					record:0,//是否认证
					package:'',
					vipname:'套餐二',//已开通vip名称
					kefu:'',
					layer_status:0,
				},
				ewm:false,
				ewmimage:'',
				openSettingBtnHidden: true,
				xian:false,
				iconlist:{
					zixun:[
						{id:0,name:"我的订单",tourl:"./orderList",url:"data:image/png;base64,"},
						{id:1,name:"我的下载",tourl:"./contractList",url:"data:image/png;base64,"},
						{id:2,name:"我的案件",tourl:"./case",url:"data:image/png;base64,"},
						{id:3,name:"我的关注",tourl:"./follow",url:"data:image/png;base64,"},
					],
				},
				iconlist1:{
					zixun:[
						{id:0,name:"受理订单",tourl:"./slfuwu",url:"data:image/png;base64,"},
						{id:1,name:"服务设置",tourl:"./serviceSet",url:"data:image/png;base64,"},
						{id:2,name:"受理案件",tourl:"./slanjian",url:"data:image/png;base64,"},
						{id:3,name:"我的关注",tourl:"./follow",url:"data:image/png;base64,"},
					],
				},
				iconlist2:{
					zixun:[
						{id:0,name:"我的分销",tourl:'./fenxiao',url:"data:image/png;base64,"},
						{id:1,name:"我的提成",tourl:'./ticheng',url:"data:image/png;base64,"},
						{id:2,name:"赚钱攻略",tourl:'./guide',url:"data:image/png;base64,"},
					],
				},
			};
		},
		onShareAppMessage(res) {
		      return {
		        title: '123律师小程序',
		        path: '/pages/index/index?scene='+uni.getStorageSync('userInfo').user_id
		      }
		    },
				onLoad(){
					if(!uni.getStorageSync('move')){
						this.xian = !this.xian
					}
				},
				onShow() {
					let that = this
					let user = uni.getStorageSync('userInfo');
					uni.request({
						url:'https://layer.boyaokj.cn/api/wechat/getUserinfo',
						method:'GET',
						data:{
							user_id:user.user_id
						},
						success(res) {
							console.log("状态")
							console.log(res.data.data)
							that.user.name = res.data.data.nickname
							that.user.headimg = res.data.data.avater
							that.user.balance = res.data.data.wallet
							that.user.layer_status = res.data.data.layer_status
							if(!res.data.data.layer){
							}else{
								that.user.lawyer = res.data.data.layer
							}
							that.user.package = res.data.data.package
						}
					})
					uni.request({
						url:'https://layer.boyaokj.cn/api/index/setting',
						method:'GEt',
						data:{
							key:'kefu'
						},
						success(res) {
							that.user.kefu = res.data.data.data
						}
					})
					uni.request({
						url:'https://layer.boyaokj.cn/api/wechat/shareCode',
						method:'GET',
						data:{
							user_id:uni.getStorageSync('userInfo').user_id
						},
						success(res) {
							// console.log("二维码")
							// console.log(res.data.data.url)
							that.ewmimage = res.data.data.url
						}
					})
				},
		methods: {
			//微信小程序保存到相册
			        handleSetting(e){
								let that = this
			            if (!e.detail.authSetting['scope.writePhotosAlbum']) {
			                  that.openSettingBtnHidden = false;
			            } else {
			                 that.openSettingBtnHidden = true;
			            }
			        },
			        saveEwm:function(e){
								let that = this
			            //获取相册授权
			               uni.getSetting({
			                 success(res) {
			                   if (!res.authSetting['scope.writePhotosAlbum']) {
			                     uni.authorize({
			                       scope: 'scope.writePhotosAlbum',
			                       success() {
			                         //这里是用户同意授权后的回调
			                         that.saveImgToLocal();
			                       },
			                       fail() {//这里是用户拒绝授权后的回调
			                           that.openSettingBtnHidden=false
			                       }
			                     })
			                   } else {//用户已经授权过了
			                     that.saveImgToLocal();
			                   }
			                 }
			               })
			        },
			        saveImgToLocal:function(e){
			            let that = this
			            uni.showModal({
			                title: '提示',
			                content: '确定保存到相册吗',
			                success: function (res) {
												console.log("第一步")
			                    if (res.confirm) {
														console.log("第二步")
			                        uni.downloadFile({
			                                url: that.ewmimage,//图片地址
			                                success: (res) =>{
																				console.log("第三步")
			                                    if (res.statusCode === 200){
			                                        uni.saveImageToPhotosAlbum({
			                                            filePath: res.tempFilePath,
			                                            success: function() {
																										console.log("第四步")
			                                                uni.showToast({
			                                                    title: "保存成功",
			                                                    icon: "none"
			                                                });
			                                            },
			                                            fail: function() {
			                                                uni.showToast({
			                                                    title: "保存失败",
			                                                    icon: "none"
			                                                });
			                                            }
			                                        });
			                                    } 
			                                }
			                            })
			                        
			                        
			                    } else if (res.cancel) {
			                        
			                    }
			                }
			            });
			            
			        },
			erm(){
				this.ewm = !this.ewm
			},
			ancl(){
				this.ewm = !this.ewm
			},
			takeOut(e){
				uni.navigateTo({
					url:'./takeOut?balance='+e
				})
			},
			bill(){
				uni.navigateTo({
					url:'./bill'
				})
			},
			open(){
				uni.navigateTo({
					url:'../single_service/otherService'
				})
			},
			toUrl1(){
				uni.navigateTo({
					url:'./yhqkeyong'
				})
			},
			toUrl2(){
				uni.navigateTo({
					url:'../renzheng/classify'
				})
			},
			toUrl3(){
				uni.navigateTo({
					url:'./problem'
				})
				
			},
			toUrl4(){
				uni.makePhoneCall({
					 phoneNumber: this.user.kefu, 
				})
			},
			toUrl5(){
				uni.navigateTo({
					url:'./about'
				})
			},
			toUrl6(){
				uni.navigateTo({
					url:'./xieyi'
				})
			},
			toPage(url){
				uni.navigateTo({
					url:url
				})
			},
			getPhoneNumber(e) {
							 let that = this 
								uni.login({
									provider: 'weixin',
									success(res) {
										uni.request({
											url:'https://layer.boyaokj.cn/api/wechat/setMobile',
											method:'GET',
											data:{
												user_id:uni.getStorageSync('userInfo').user_id,
												code:res.code,
												iv:e.detail.iv,
												encrypteddata:e.detail.encryptedData
											},
											success(res) {
												that.xian = !that.xian
												uni.setStorageSync('move',1)
												uni.switchTab({
												    url: '../index/index'
												});
											}
										})
									}
								})
			           },
		}
	};
</script>

<style>
	page{
		background: #F8F8F8;
	}
	.functionlist {
		margin-top: 24rpx;
	}
	.buke{
		pointer-events: none;
	}
	.bendi{
		background: #4169E1;
		color: #fff;
		border-radius: 0;
	}
	.baidi{
