		width: 24rpx;
		    height: 12rpx;
		    /* -webkit-transform: rotate(-90deg); */
		    transform: rotate(-90deg);
		    margin-left: 7rpx;
		    margin-top: 11rpx;
	}
	.balance view:nth-child(1){
		font-size: 40rpx;
		font-family: DINCondensed-Bold, DINCondensed;
		font-weight: bold;
		color: #E1B12F;
		text-align: center;
		    margin-top: 15rpx;
	}
	.balance view:nth-child(2){
		font-size: 24rpx;
		font-family: PingFangSC-Medium, PingFang SC;
		font-weight: 500;
		color: #FFFFFF;
