		margin-top: -100rpx;
	}
	.vipdet{
		margin: 12rpx 0 0 23rpx;
	}
	.vipdet view:nth-child(1){
		font-size: 24rpx;
		font-family: PingFangSC-Regular, PingFang SC;
		font-weight: 400;
		color: #FFFFFF;	
	}
	.vipdet view:nth-child(2){
		font-size: 20rpx;
		font-family: PingFangSC-Regular, PingFang SC;
		font-weight: 400;
		color: #FFFFFF;
	}
	.kaitongbtn{
		width: 124rpx;
		height: 41rpx;
		background: #6765FF;
		box-shadow: 0px 2px 4px 0px rgba(109, 109, 109, 0.5);
		border-radius: 21rpx;
		font-size: 24rpx;
		font-family: PingFangSC-Regular, PingFang SC;
		font-weight: 400;
		color: #FFFFFF;
		/* line-height: 33rpx; */
		text-align: center;
		margin: 25rpx 30rpx 0 0;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.tuigaung{
		width: 710rpx;
		height: 202rpx;
		background: #FFFFFF;
		box-shadow: 0px 0px 19px 0px rgba(0, 0, 0, 0.11);
		border-radius: 16rpx;
		background: #fff;
		margin: auto;
		margin-top: 16rpx;
	}
	.tgtext{
		font-size: 24rpx;
		font-family: PingFangSC-Medium, PingFang SC;
		font-weight: 500;
		color: #6A6A6A;
		width: 664rpx;
		margin: auto;
		border-bottom: 1px solid #DDDDDD;
		padding: 10rpx 0;
	}
	.tuigaung .zixunitemimage {
		width: 48rpx;
		height: 48rpx;
	}
	.tuigaung .zixunitemview {
